/* eslint-disable react/no-array-index-key */
import React, { useEffect, useCallback, useState } from "react";
import { Link as RouterLink, useNavigate, useParams } from "react-router-dom";

import {
  Avatar,
  Box,
  Button,
  CircularProgress,
  Container,
  Drawer,
  IconButton,
  InputAdornment,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  Typography,
} from "@material-ui/core";
import axios from "axios";
import SearchIcon from "../../icons/Search";
import XIcon from "../../icons/X";
import Scrollbar from "../Scrollbar";
import wait from "../../utils/wait";
import {customAxios} from "../../mangement/apis/Axios";
import useMounted from "../../hooks/useMounted";
import getInitials from "../../utils/getInitials";

const ContentSearch = () => {
  const mounted = useMounted();
const navigate=useNavigate();
  const [value, setValue] = useState("");
  const [open, setOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showResults, setShowResults] = useState(false);
  const [rows, setrows] = useState([]);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setValue("")
  };

  const Addmission_date = async() => {
    try {
      const Data = await customAxios.getCall("list/search");
      const result = await Data.filter((user) => {
        return (
           value && user && user.name && user.name.toLowerCase().includes(value)
        );
      });
        setrows(result);

      if (mounted.current) {
        // setrows(result);
        console.log(result);
      }
    } catch (err) {
      console.log(err);
    }
  } ;

  useEffect(() => {}, []);
  const search = async () => {
    setShowResults(false);
    setIsLoading(true);
    // Do search here
    await wait(1500);
    
    setIsLoading(false);
    Addmission_date();
    setShowResults(true);
  };

  const handleClick = () => {
    search();
  };

  const handleKeyUp = (event) => {
    console.log(event.code)
    if (event.code === "ENTER") {
      search();
    }
  };

  return (
    <>
      <Tooltip title="Search">
        <IconButton color="inherit" onClick={handleOpen}>
          <SearchIcon fontSize="small" />
        </IconButton>
      </Tooltip>
      <Drawer
        anchor="top"
        ModalProps={{ BackdropProps: { invisible: true } }}
        onClose={handleClose}
        open={open}
        PaperProps={{
          sx: { width: "100%" },
        }}
        variant="temporary"
      >
        <Box sx={{ p: 1 }}>
          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            <IconButton onClick={handleClose}>
              <XIcon fontSize="small" />
            </IconButton>
          </Box>
        </Box>
        <Box>
          <Container maxWidth="md">
            <Box
              sx={{
                alignItems: "center",
                display: "flex",
              }}
            >
              <TextField
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon fontSize="small" />
                    </InputAdornment>
                  ),
                }}
                onChange={(event) => setValue(event.target.value)}
                onKeyUp={handleKeyUp}
                placeholder="Search..."
                value={value}
              />
              <Button
                color="primary"
                onClick={handleClick}
                size="large"
                sx={{ ml: 2 }}
                variant="contained"
              >
                Search
              </Button>
            </Box>
            <Box sx={{ mt: 3 }}>
              <Scrollbar options={{ suppressScrollX: true }}>
                {isLoading ? (
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <CircularProgress />
                  </Box>
                ) : (
                  <>
                    {showResults && (
                      <Table>
                        <TableHead>
                          <TableRow>
                            <TableCell>Name</TableCell>
                            <TableCell>Father Name</TableCell>
                            <TableCell>Class</TableCell>
                            <TableCell>Gender</TableCell>
                            <TableCell>Phone</TableCell>
                          </TableRow>
                        </TableHead>

                        <>
                          {rows && rows.length > 0
                            ? rows.map((row, i) => (
                                <>
                                  <TableBody>
                                    <TableRow
                                      onClick={() =>
                                        navigate("/dashboard/Student-detail", {
                                          state: { row },
                                        })
                                      }
                                      hover
                                      key={i}
                                    >
                                      <TableCell>
                                        <Box
                                          sx={{
                                            alignItems: "center",
                                            display: "flex",
                                          }}
                                        >
                                          <Avatar
                                            src={`http://localhost:8000/uploads/${row.student_image}`}
                                            sx={{
                                              height: 42,
                                              width: 42,
                                            }}
                                          >
                                            {getInitials(row.name)}
                                          </Avatar>
                                          <Box sx={{ ml: 1 }}>
                                            <Link
                                              color="textPrimary"
                                              component={RouterLink}
                                              to="#"
                                              underline="none"
                                              variant="subtitle2"
                                            >
                                              {row.name}
                                            </Link>
                                            <Typography
                                              color="textSecondary"
                                              variant="body2"
                                            >
                                              {row.email}
                                            </Typography>
                                          </Box>
                                        </Box>
                                      </TableCell>
                                      <TableCell>{row.fname}</TableCell>{" "}
                                      <TableCell>{row.class}</TableCell>{" "}
                                      <TableCell>{row.gender}</TableCell>{" "}
                                      <TableCell>{row.phone}</TableCell>
                                    </TableRow>
                                  </TableBody>
                                </>
                              ))
                            : "Data not found"}
                        </>
                      </Table>
                    )}
                  </>
                )}
              </Scrollbar>
            </Box>
          </Container>
        </Box>
      </Drawer>
    </>
  );
};

export default ContentSearch;
