import { createContext, useEffect, useReducer } from "react";
import PropTypes from "prop-types";
import { authApi } from "../__fakeApi__/authApi";
import axios from "axios";
import toast from "react-hot-toast";
import {customAxios} from "../mangement/apis/Axios";

const initialState = {
  isAuthenticated: false,
  isInitialized: false,
  user: null,
};

const handlers = {
  INITIALIZE: (state, action) => {
    const { isAuthenticated, user } = action.payload;

    return {
      ...state,
      isAuthenticated,
      isInitialized: true,
      user,
    };
  },
  LOGIN: (state, action) => {
    const { user } = action.payload;

    return {
      ...state,
      isAuthenticated: true,
      user,
    };
  },
  LOGOUT: (state) => ({
    ...state,
    isAuthenticated: false,
    user: null,
  }),
  REGISTER: (state, action) => {
    const { user } = action.payload;

    return {
      ...state,
      isAuthenticated: true,
      user,
    };
  },
};

const reducer = (state, action) =>
  handlers[action.type] ? handlers[action.type](state, action) : state;

const AuthContext = createContext({
  ...initialState,
  platform: "JWT",
  login: () => Promise.resolve(),
  logout: () => Promise.resolve(),
  register: () => Promise.resolve(),
});

export const AuthProvider = (props) => {
  const { children } = props;
  const [state, dispatch] = useReducer(reducer, initialState);
  console.log(",,", state);
  useEffect(() => {
    const initialize = async () => {
      try {
        const accessToken = window.localStorage.getItem("accessToken");

        if (accessToken) {
          // const user = await authApi.me(accessToken);
        const user = await JSON.parse(window.localStorage.getItem("user"));
         
          dispatch({
            type: "INITIALIZE",
            payload: {
              isAuthenticated: true,
              user,
            },
          });
        } else {
          dispatch({
            type: "INITIALIZE",
            payload: {
              isAuthenticated: false,
              user: null,
            },
          });
        }
      } catch (err) {
        console.error(err);
        dispatch({
          type: "INITIALIZE",
          payload: {
            isAuthenticated: false,
            user: null,
          },
        });
      }
    };

    initialize();
  }, []);

  const login = async (email, password) => {
    console.log(email, password);
    // const accessToken = await authApi.login({ email, password });
    // const user = await authApi.me(accessToken);
    const res = await customAxios.postCalls("register/login", {
      email,
      password,
    });

    let user = res;
    // console.log("user", user);
    // return
     if (user.status === 426) {
      return  toast.error(user.data.msj);
     }
    else if(user.status===401){
      return toast.error("Incorrect Email Or password")
    }
    else if (user.status === 200) {
      localStorage.setItem("user",JSON.stringify(user.data.registerdUser));
      localStorage.setItem("accessToken",JSON.stringify(user.data.token)
      );
      toast.success("Login Successfully");

    } else {
      return new Error("unAthorized Date");
    }

    dispatch({
      type: "LOGIN",
      payload: {
        user,
      },
    });
  };

  const logout = async () => {
    localStorage.removeItem("accessToken");
    localStorage.removeItem("user");
    dispatch({ type: "LOGOUT" });
  };

  const register = async (email,name,password,lastname,InstituteName,countryCode,contact) => {
    const res = await customAxios.postCall2("register/api", {
      email,
      name,
      password,
      lastname,
      InstituteName,
      countryCode,
      contact,
    });
const user=res
 if(user.status==409){
  toast.error("User Already Exist")
 }else{
  toast.success("User Successfully Register ")
 }

    dispatch({
      type: "REGISTER",
      payload: {
        user,
      },
    });
  };

  return (
    <AuthContext.Provider
      value={{
        ...state,
        platform: "JWT",
        login,
        logout,
        register,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

AuthProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

export default AuthContext;
