import { useRef, useState, useEffect } from "react";
import AppsIcon from "@material-ui/icons/Apps";
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import GroupAddIcon from "@material-ui/icons/GroupAdd";
import DashboardIcon from "@material-ui/icons/Dashboard";
import TextsmsIcon from "@material-ui/icons/Textsms";
import ReceiptIcon from "@material-ui/icons/Receipt";
import { Link, Link as RouterLink } from "react-router-dom";

import { IconButton, Popover, Tooltip, Typography } from "@material-ui/core";

const Multiple_Apps = () => {
  const anchorRef = useRef(null);
  // console.log("1", anchorRef);
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Tooltip title="Contacts">
        <IconButton color="inherit" onClick={handleOpen} ref={anchorRef}>
          <AppsIcon fontSize="medium" />
        </IconButton>
      </Tooltip>
      <Popover
        anchorEl={anchorRef.current}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        onClose={handleClose}
        open={open}
        PaperProps={{
          sx: {
            p: 2,
            width: 320,
            height: 400,
          },
        }}
      >
        <Typography sx={{ ml: 2 }} color="textPrimary" variant="h6">
          Apps
        </Typography>
        <div onClick={handleClose}>
          <IconButton style={{ marginLeft: 5 }}>
            <Link color="red" component={RouterLink} to="dashboard/maindash">
              <DashboardIcon fontSize="large" />
            </Link>
          </IconButton>{" "}
          <IconButton>
            <Link component={RouterLink} to="dashboard/all-students">
              <GroupAddIcon fontSize="large" />
            </Link>
          </IconButton>{" "}
          <IconButton>
            <Link component={RouterLink} to="dashboard/sms">
              <TextsmsIcon fontSize="large" />
            </Link>
          </IconButton>{" "}
          <IconButton>
            <Link component={RouterLink} to="dashboard/fee">
              <ReceiptIcon fontSize="large" />
            </Link>
          </IconButton>
        </div>
      </Popover>
    </>
  );
};

export default Multiple_Apps;
